// /api/curriculum-datos-publico



async function consultarAPIpublica() {

    try {
        const url = '/api/curriculum-datos-publico';
        const resultado = await fetch(url);

        const curriculumDatosPublico = await resultado.json();//saca el json de la Api Controller que Obtiene todos los curriculumDatos

        mostrarCurriculumDatosPublico(curriculumDatosPublico);
    } catch (error) {
        console.log(error);
    }
}

function mostrarCurriculumDatosPublico(curriculumDatosPublico) {
    curriculumDatosPublico.forEach( curriculum => {
        const {empresa, empresa_info, id, perfil, administrarId, proyecto_info, html, css, js, php, gulp, sass, adobe_xd, mysql} = curriculum;//son las variables que contiene los valores de los 

         //Perfil
         const divPerfil = document.createElement('DIV');
         divPerfil.classList.add('parte-contenido');

         const h3 = document.createElement('H3');
         h3.textContent = "Perfil";
         const parrafoPerfil = document.createElement('P');
         parrafoPerfil.textContent = curriculum.perfil;//texto Perfil
        //  parrafoPerfil.classList.add('hacer-hover-editar');
         
         //Experiencia
         const h5_empresa_nombre = document.createElement('H5');
         h5_empresa_nombre.textContent = curriculum.empresa_info;
        //  h5_empresa_nombre.classList.add('hacer-hover-editar');

         const parrafoExperiencia = document.createElement('P');
         parrafoExperiencia.textContent = curriculum.empresa;//texto experiencia de la empresa
        //  parrafoExperiencia.classList.add('hacer-hover-editar');

         //Proyectos personales
         const h5_proyecto = document.createElement('H5');
         h5_proyecto.textContent = "Proyectos Personales";

         const p_proyectos_info = document.createElement('P');
         p_proyectos_info.textContent = curriculum.proyecto_info;//texto proyecto info
        //  p_proyectos_info.classList.add('hacer-hover-editar');

         //tecnologias experiencia (progreso)
         const P_html = document.createElement("PROGRESS");
         P_html.setAttribute("value", curriculum.html);
         P_html.setAttribute("max", "100");
         P_html.classList.add("progreso");

         const P_css = document.createElement("PROGRESS");
         P_css.setAttribute("value", curriculum.css);
         P_css.setAttribute("max", "100");
         P_css.classList.add("progreso");

         const P_js = document.createElement("PROGRESS");
         P_js.setAttribute("value", curriculum.js);
         P_js.setAttribute("max", "100");
         P_js.classList.add("progreso");

         const P_php = document.createElement("PROGRESS");
         P_php.setAttribute("value", curriculum.php);
         P_php.setAttribute("max", "100");
         P_php.classList.add("progreso");

         const P_gulp = document.createElement("PROGRESS");
         P_gulp.setAttribute("value", curriculum.gulp);
         P_gulp.setAttribute("max", "100");
         P_gulp.classList.add("progreso");

         const P_sass = document.createElement("PROGRESS");
         P_sass.setAttribute("value", curriculum.sass);
         P_sass.setAttribute("max", "100");
         P_sass.classList.add("progreso");

         const P_adobe_xd = document.createElement("PROGRESS");
         P_adobe_xd.setAttribute("value", curriculum.adobe_xd);
         P_adobe_xd.setAttribute("max", "100");
         P_adobe_xd.classList.add("progreso");

         const P_mysql = document.createElement("PROGRESS");
         P_mysql.setAttribute("value", curriculum.mysql);
         P_mysql.setAttribute("max", "100");
         P_mysql.classList.add("progreso");

         //aqui se inserta en el HTML
         const perfilContenido = document.querySelector("#perfil-contendio");
         perfilContenido.appendChild(divPerfil);
         divPerfil.appendChild(h3);
         divPerfil.appendChild(parrafoPerfil);
         
         const experiencia = document.querySelector("#experiencia");
         experiencia.appendChild(h5_empresa_nombre);
         experiencia.appendChild(parrafoExperiencia);

         const proyectosPersonales = document.querySelector("#proyectos-personales");
         proyectosPersonales.appendChild(h5_proyecto);
         proyectosPersonales.appendChild(p_proyectos_info);

         //tecnologias que se insertan en el HTML
         const tecnologiaHtml = document.querySelector("#tecnologia_html");
         tecnologiaHtml.appendChild(P_html);

         const tecnologiaCss = document.querySelector("#tecnologia_css");
         tecnologiaCss.appendChild(P_css);

         const tecnologiaJs = document.querySelector("#tecnologia_js");
         tecnologiaJs.appendChild(P_js);

         const tecnologiaPhp = document.querySelector("#tecnologia_php");
         tecnologiaPhp.appendChild(P_php);

         const tecnologiaGulp = document.querySelector("#tecnologia_gulp");
         tecnologiaGulp.appendChild(P_gulp);

         const tecnologiaSass = document.querySelector("#tecnologia_sass");
         tecnologiaSass.appendChild(P_sass);

         const tecnologiaAdobeXd = document.querySelector("#tecnologia_adobe_xd");
         tecnologiaAdobeXd.appendChild(P_adobe_xd);

         const tecnologiaMysql = document.querySelector("#tecnologia_mysql");
         tecnologiaMysql.appendChild(P_mysql);


        // document.querySelector('#servicios').appendChild(servicioDiv);//aqui es donde se agrega al html y se imprime en la pagina

    });
}

